
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
  margin: 20px auto;
  width: 800px;
  height: 600px;
  overflow: hidden;
  border-radius: 3px;
}
.main {
  float: left;
  width: 600px;
  height: 100%;
  border: solid 1px black;
  /*background-color: #a4a4a4;*/
}
.sidebar {
  margin-left: 600px;
  color: #f4f4f4;
  background-color: #2e3238;
  height: 600px;
}
#members {
  height: 150px;
}
#qrcode {
  width: 100px;  
  height: 130px;
}



